import { ElementRef } from '@angular/core';
import { MazeData } from '../../provider/mazedata';
import { MazeBlocks } from './mazeblocks';
declare var Blockly: any;

export class Blockpro {
    boocklyWorksapce: any;
    blocklyArea: ElementRef;
    blocklyDiv: ElementRef;
    mazeData: MazeData;
    mazeBlocks: MazeBlocks;

    public init(ba: ElementRef, bd: ElementRef, md: MazeData) {
      this.blocklyArea = ba;
      this.blocklyDiv = bd;
      this.mazeData = md;

      this.mazeBlocks = new MazeBlocks();
      this.mazeBlocks.init(this.mazeData);
      
      var toolbox_cfg = this.mazeData.TOOLBOX;
      this.boocklyWorksapce = Blockly.inject(
          this.blocklyDiv.nativeElement, 
          {
              media: 'thirdparty/blockly/media/',
              toolbox: toolbox_cfg
          }
      );
      Blockly.svgResize(this.boocklyWorksapce);
    }

    public reset() {
        this.boocklyWorksapce.updateToolbox(this.mazeData.TOOLBOX);
    }

    public run() {
      var code = Blockly.JavaScript.workspaceToCode(this.boocklyWorksapce);
      console.log(code);
      eval(code);
    }
}


