import { Messages } from '../../provider/messages';
import { MazeData } from '../../provider/mazeData';
declare var Blockly: any;

/**
 * @fileoverview Blocks for Blockly's Maze application.
 * @author fraser@google.com (Neil Fraser)
 */
export class MazeBlocks {
  /**
   * Common HSV hue for all movement blocks.
   */
  public MOVEMENT_HUE = 290;

  /**
   * HSV hue for loop block.
   */
  public LOOPS_HUE = 120;

  /**
   * Common HSV hue for all logic blocks.
   */
  public LOGIC_HUE = 210;

  /**
   * Left turn arrow to be appended to messages.
   */
  public LEFT_TURN = ' \u21BA';

  /**
   * Left turn arrow to be appended to messages.
   */
  public RIGHT_TURN = ' \u21BB';

  public mazeData: MazeData;

  // Extensions to Blockly's language and JavaScript generator.
  public init(mazeData) {
    var self = this;
    self.mazeData = mazeData;
    Blockly.Blocks['maze_moveForward'] = {
      /**
       * Block for moving forward.
       * @this Blockly.Block
       */
      init: function() {
        this.jsonInit({
          "message0": Messages.getMsg('Maze_moveForward'),
          "previousStatement": null,
          "nextStatement": null,
          "colour": self.MOVEMENT_HUE,
          "tooltip": Messages.getMsg('Maze_moveForwardTooltip')
        });
      }
    };
    
    Blockly.JavaScript['maze_moveForward'] = function(block) {
      // Generate JavaScript for moving forward.
      return 'maze_player.moveForward(\'block_id_' + block.id + '\');\n';
    };
    
    Blockly.Blocks['maze_turn'] = {
      /**
       * Block for turning left or right.
       * @this Blockly.Block
       */
      init: function() {
        var DIRECTIONS =
            [[Messages.getMsg('Maze_turnLeft'), 'turnLeft'],
              [Messages.getMsg('Maze_turnRight'), 'turnRight']];
        // Append arrows to direction messages.
        DIRECTIONS[0][0] += self.LEFT_TURN;
        DIRECTIONS[1][0] += self.RIGHT_TURN;
        this.setColour(self.MOVEMENT_HUE);
        this.appendDummyInput()
            .appendField(new Blockly.FieldDropdown(DIRECTIONS), 'DIR');
        this.setPreviousStatement(true);
        this.setNextStatement(true);
        this.setTooltip(Messages.getMsg('Maze_turnTooltip'));
      }
    };
    
    Blockly.JavaScript['maze_turn'] = function(block) {
      // Generate JavaScript for turning left or right.
      var dir = block.getFieldValue('DIR');
      return 'maze_player.' + dir + '(\'block_id_' + block.id + '\');\n';
    };
    
    Blockly.Blocks['maze_if'] = {
      /**
       * Block for 'if' conditional if there is a path.
       * @this Blockly.Block
       */
      init: function() {
        var DIRECTIONS =
            [[Messages.getMsg('Maze_pathAhead'), 'maze_player.isPathForward'],
              [Messages.getMsg('Maze_pathLeft'), 'maze_player.isPathLeft'],
              [Messages.getMsg('Maze_pathRight'), 'maze_player.isPathRight']];
        // Append arrows to direction messages.
        DIRECTIONS[1][0] += self.LEFT_TURN;
        DIRECTIONS[2][0] += self.RIGHT_TURN;
        this.setColour(self.LOGIC_HUE);
        this.appendDummyInput()
            .appendField(new Blockly.FieldDropdown(DIRECTIONS), 'DIR');
        this.appendStatementInput('DO')
            .appendField(Messages.getMsg('Maze_doCode'));
        this.setTooltip(Messages.getMsg('Maze_ifTooltip'));
        this.setPreviousStatement(true);
        this.setNextStatement(true);
      }
    };
    
    Blockly.JavaScript['maze_if'] = function(block) {
      // Generate JavaScript for 'if' conditional if there is a path.
      var argument = block.getFieldValue('DIR') +
          '(\'block_id_' + block.id + '\')';
      var branch = Blockly.JavaScript.statementToCode(block, 'DO');
      var code = 'if (' + argument + ') {\n' + branch + '}\n';
      return code;
    };
    
    Blockly.Blocks['maze_ifElse'] = {
      /**
       * Block for 'if/else' conditional if there is a path.
       * @this Blockly.Block
       */
      init: function() {
        var DIRECTIONS =
            [[Messages.getMsg('Maze_pathAhead'), 'maze_player.isPathForward'],
              [Messages.getMsg('Maze_pathLeft'), 'maze_player.isPathLeft'],
              [Messages.getMsg('Maze_pathRight'), 'maze_player.isPathRight']];
        // Append arrows to direction messages.
        DIRECTIONS[1][0] += self.LEFT_TURN;
        DIRECTIONS[2][0] += self.RIGHT_TURN;
        this.setColour(self.LOGIC_HUE);
        this.appendDummyInput()
            .appendField(new Blockly.FieldDropdown(DIRECTIONS), 'DIR');
        this.appendStatementInput('DO')
            .appendField(Messages.getMsg('Maze_doCode'));
        this.appendStatementInput('ELSE')
            .appendField(Messages.getMsg('Maze_elseCode'));
        this.setTooltip(Messages.getMsg('Maze_ifelseTooltip'));
        this.setPreviousStatement(true);
        this.setNextStatement(true);
      }
    };
    
    Blockly.JavaScript['maze_ifElse'] = function(block) {
      // Generate JavaScript for 'if/else' conditional if there is a path.
      var argument = block.getFieldValue('DIR') +
          '(\'block_id_' + block.id + '\')';
      var branch0 = Blockly.JavaScript.statementToCode(block, 'DO');
      var branch1 = Blockly.JavaScript.statementToCode(block, 'ELSE');
      var code = 'if (' + argument + ') {\n' + branch0 +
                  '} else {\n' + branch1 + '}\n';
      return code;
    };
    
    Blockly.Blocks['maze_forever'] = {
      /**
       * Block for repeat loop.
       * @this Blockly.Block
       */
      init: function() {
        this.setColour(self.LOOPS_HUE);
        this.appendDummyInput()
            .appendField(Messages.getMsg('Maze_repeatUntil'))
            .appendField(new Blockly.FieldImage('assets/' + self.mazeData.SKIN.marker, 12, 16));
        this.appendStatementInput('DO')
            .appendField(Messages.getMsg('Maze_doCode'));
        this.setPreviousStatement(true);
        this.setTooltip(Messages.getMsg('Maze_whileTooltip'));
      }
    };
    
    Blockly.JavaScript['maze_forever'] = function(block) {
      // Generate JavaScript for repeat loop.
      var branch = Blockly.JavaScript.statementToCode(block, 'DO');
      if (Blockly.JavaScript.INFINITE_LOOP_TRAP) {
        branch = Blockly.JavaScript.INFINITE_LOOP_TRAP.replace(/%1/g,
            '\'block_id_' + block.id + '\'') + branch;
      }
      return 'maze_player.notDone(function() {' + 
        branch + 
      '})\n';
    };
  }

}



