import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Canvas } from './canvas';
import { Blockpro } from './blockpro';
import { MazeData } from '../../provider/mazedata';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  @ViewChild('canvasArea') canvasArea: ElementRef;
  @ViewChild('blocklyArea') blocklyArea: ElementRef;
  @ViewChild('blocklyDiv') blocklyDiv: ElementRef;
  canvas: Canvas;
  blockpro: Blockpro;
  isRun: boolean;

  constructor(
    public navCtrl: NavController, 
    private mazeData: MazeData
    ) {
      this.canvas = new Canvas();
      this.blockpro = new Blockpro;
      this.isRun = true;
  }

  ngAfterViewInit(){
    this.canvas.init(this.canvasArea, this.mazeData);
    this.blockpro.init(this.blocklyArea, this.blocklyDiv, this.mazeData);
  }

  onLevelBtnClicked(btn, level) {
    this.mazeData.resetLevel(level);
    this.canvas.reset();
    this.blockpro.reset();
  }

  onRunBtnClicked(btn){
    if (this.isRun) {
      this.blockpro.run();
      this.isRun = false;
      btn._elementRef.nativeElement.innerText = "Reset";
    }
    else {
      this.canvas.reset();
      this.isRun = true;
      btn._elementRef.nativeElement.innerText = "Run";
    }
  }
}



